import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { alertCommunicationError } from 'src/app/commons/user-feedback';
import { Epp } from 'src/app/entities/epp';
import { ContextService } from 'src/app/services/context.service';
import { EppService } from 'src/app/services/epp.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-epp',
  templateUrl: './epp.page.html',
  styleUrls: ['./epp.page.scss'],
})
export class EppPage implements OnInit {

  epps: any = [];

  nit: number;

  documento: number;

  opcion: string;
  
  private eppSubscription: Subscription;

  constructor(
    public eppService: EppService,
    private alertCtrl: AlertController,
    private context: ContextService,
    private router: Router,
    private http: HttpClient
  ) { }

  ngOnInit(): void {
    this.context.get().then(c => {
      this.nit = c.empresa.id;
      this.documento = c.trabajador.id;

      this.eppService.getAll(this.nit, this.documento).subscribe((result: any) => {
        var epps: any;
        for(var i = 0; i < result.length; i++) {

          epps = {
            id: result[i].Pk_Id_EPP,
            nombre: result[i].NombreEPP,
            uso: result[i].Uso,
            image: ''
          };
          this.epps.push(epps);
        }

        this.cargarImagenes();

      });
    });
  }

  ionViewWillEnter(): void {
    this.opcion = 'epp';
  }

  ionViewDidLeave(): void {
    this.opcion = undefined;
  }

  isLoading(): boolean {
    return this.epps === undefined;
  }

  noData(): boolean {
    return !this.isLoading() && this.epps.length === 0;
  }

  pushEppDetalle(epp: Epp): void {
    const url = this.eppService.getUrlEpp(this.nit, epp.id);
    this.router.navigate(['/epp-details'], { state: { epp, urlImgEpp: url }});
  }

  async cargarImagenes() {

    for (var i = 0; i < this.epps.length; i++) {

      var url = this.eppService.getUrlEpp(this.nit, this.epps[i].id);

        try {
          this.epps[i].image = await this.http.get<any>(url).pipe().toPromise();
        } catch (error) {
          this.epps[i].image = "";
        }

    }
    
  }

}
