import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ContextService } from './context.service';

@Injectable({
  providedIn: 'root'
})
export class EppService {

  constructor(
    private http: HttpClient,
    private context: ContextService
  ) { }

  getAll(nit: number, documento: number): Observable<any> {

    const url = this.context.getEnvUrl().call(null, 'participacion') + '/ReporteServicio/Obtener-ListaEPP';

    const body = {
      Nit: nit,
      Documento: documento
    };
  
    return this.http.post(url, body);
    
  }

  getUrlEpp(nit: number, idEpp: number) {
  
    const url = this.context.getEnvUrl().call(null, 'participacion') + '/ReporteServicio/Obtener-ImagenEPP'
    + '?Nit=' + nit
    + '&IdEPP=' + idEpp;
    return url;
   }

}
